.services {
    padding: 100px 0;

    .services__wrapper {
        margin: 0 auto;

        .services__title {
            text-align: center;
            margin-bottom: 50px;

            h1 {
                @include bold();
                font-size: 42px;
                color: $blue;
                margin-bottom: 15px;    
            }
            p {
                @include reg();
                font-size: 15px;
                color: $gray;
            }
        }

        .services__service {
            width: 65%;
            border-radius: 8px;
            padding: 20px 30px;
            box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.13);
            margin: 0 auto 20px auto;
            cursor: pointer;
            transition: 0.15s;
            position: relative;

            .service__header {
                align-items: center;
                display: flex;

                i {
                    color: $blue;
                    margin-right: 30px;
                }
                p {
                    @include bold();
                    color: $blue;
                    font-size: 15px;
                    margin: 0;
                }
                .fa-sort-down {
                    position: absolute;
                    margin-right: 0;
                    right: 30px;
                }
            }    

            .service__content {
                @include reg();
                margin-top: 20px;
                font-size: 15px;
                display: flex;
                align-items: center;

                img {
                    max-height: 155px;
                    max-width: auto;
                    margin-right: 25px;
                }
            }

            &:hover {
                transform: scale(1.02);
            }
        }
    }
}

  

